export default (strong) => {
  let color
  switch (strong) {
    case '':
      color = '#00ff00'
      break
    case '':
      color = '#0000ff'
      break
    case '':
      color = '#ffff00'
      break
    case '':
      color = '#ff9900'
      break
    case '':
      color = '#ff00ff'
      break
    case '':
      color = '#ff0000'
      break
    default:
      color = '#ffffff'
      break
  }
  return color
}
